// one off
.post_mortem, .key {
  li {
    list-style-type: none; }
  .why {
    padding-left: 2em; }
  .yes {
    background: #FFF7ED; }
  .no {
    color: #444; }
  .na {
    background: #E9E9E9; } }

// one off for story boxes
.story {
  padding: 1em;
  overflow: auto;
  margin-bottom: 1em;
  p {
    float: left;
    width: 24em;
    margin-bottom: 0; } }

.bad {
  background: #F8C6AF; }
.good {
  background: #C0F8AF; }

.smiley {
  font-family: "Helvetica";
  font-size: 4em;
  float: left;
  width: 1em; }

.footnotes {
  font-size: 0.9em;
  color: #333; }

.twitter-tweet {
  width: 100%;
  white-space: normal;
  margin: 0; }

.content {
  img, video, iframe {
    /* scale the image proportionally, but never larger than container */
    max-width: 100%;
    height: auto; } }


blockquote p {
  margin: 0;
  white-space: normal; }
